body {
  height: 100vh;
  justify-content: flex-start;
  align-items: stretch;
  display: flex;
}

nav {
  width: 64px;
  z-index: 9999;
  background: #fbfbfb;
  flex-grow: 0;
  flex-shrink: 0;
  position: relative;
}

.nav--goback {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  margin: -14px -14px auto;
  position: absolute;
  top: 50%;
  left: 50%;
}

.nav--goback svg {
  margin: -6px -5px auto;
  position: absolute;
  top: 50%;
  left: 50%;
}

#profile {
  width: 510px;
  box-sizing: border-box;
  margin: auto;
  position: relative;
}

